import React from 'react';

class EmployeesList extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        const { employees } = this.props;
        return (
            <tbody>
                {employees.map((e, i) => {
                    return (
                        <tr key={i}>
                            <td>{e.id}</td>
                            <td>{e.name}</td>
                            <td>{e.title}</td>
                            <td>{e.email}</td>
                        </tr>
                    )
                })}
            </tbody>
        );
    }
}

export default EmployeesList;