require('es6-promise').polyfill();
import fetch from 'isomorphic-fetch';

export function getJobTitles() {
    return fetch(`http://localhost:5002/titles`)
        .then(function (resp) {
            return resp.json()
        })
        .catch(err => {
            console.log(err);
        });
}

export function getAllEmployees() {
    return fetch(`http://localhost:5002/employee`)
}

export function createNewEmployee(name, titleId, email) {
    return fetch(`http://localhost:5002/employee/`, {
        method: 'PUT',
        body: `{"name": ${name},"title_id": ${titleId},"email": ${email}}`
    })
        .then(function (resp) {
            return resp.json()
        })
        .catch(err => {
            console.log(err);
        });
}

export function updateEmployee(id, name, titleId, email) {
    return fetch(`http://localhost:5002/employee/${id}`, {
        method: 'PUT',
        body: `{"name": ${name},"title_id": ${titleId},"email": ${email}}`
    })
        .then(function (resp) {
            return resp.json()
        })
        .catch(err => {
            console.log(err);
        });
}