import * as types from './actionTypes';
import * as api from '../api/employee'

export const fetchAllEmployeeSuccess = (employees) => ({
    type: types.FETCH_ALL_EMPLOYEES_SUCCESS,
    employees
});

export const createNewEmployeeSuccess = (employee) => ({
    type: types.CREATE_EMPLOYEE_SUCCESS,
    employee
});

export const updateNewEmployeeSuccess = (employee) => ({
    type: types.UPDATE_EMPLOYEE_SUCCESS,
    employee
});


export const fetchAllEmployees = () => (dispatch) => {
    return fetch(`http://localhost:5002/employee`)
        .then(function (resp) {
            return resp.json()
        })
        .then(employees => {
            dispatch(fetchAllEmployeeSuccess(employees));
        })
        .catch(err => {
            dispatch({ type: types.FETCH_ALL_EMPLOYEES_FAILURE });
            console.log(err);
        });
};

export const createNewEmployee = (name, titleId, email) => (dispatch) => {
    return fetch(`http://localhost:5002/employee/`, {
        method: 'PUT',
        body: `{"name": ${name},"title_id": ${titleId},"email": ${email}}`
    })
        .then(function (resp) {
            return resp.json()
        })
        .then(status => {
            dispatch(createNewEmployeeSuccess(status));
        })
        .catch(err => {
            dispatch({ type: types.CREATE_EMPLOYEE_FAILURE });
            console.log(err);
        });
}

export const updateEmployee = (id, name, titleId, email) => (dispatch) => {
    return fetch(`http://localhost:5002/employee/${id}`, {
        method: 'PUT',
        body: `{"name": ${name},"title_id": ${titleId},"email": ${email}}`
    })
        .then(function (resp) {
            return resp.json()
        })
        .then(status => {
            dispatch(updateEmployeeSuccess(status));
        })
        .catch(err => {
            dispatch({ type: types.UPDATE_EMPLOYEE_FAILURE });
            console.log(err);
        });
};
